@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
.sidebar {
    display: flex;
    flex-direction: column;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background-color: rgb(54, 9, 73);
    z-index: 1;
}

.sb-item {
    display: flex;
    width: 100%;
    height: 2.5em;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: whitesmoke;
}

.sb-item>span {
    font-size: 1em;
    padding-left: 0.2em;
}

.sb-item:active>.sb-icon {
    transform: translateY(5px);
}

.sb-item:active>span {
    transform: translateY(5px);
}

.sb-item:hover {
    background-color: rgb(231, 73, 99);
}

.sb-active {
    background-color: rgb(231, 73, 99);
}

.sb-icon {
    width: 1em;
    height: 1em;
    color: whitesmoke;
}

/*Modern Mobile*/
@media only screen and (min-width: 520px) {
    .sidebar {
        position: fixed;
        width: 100vw;
        justify-content: space-between;
        min-height: 5.5em;
        transition: min-height 0.5s;
        flex-direction: row;
    }

    .sidebar>div {
        display: flex;
    }

    .sb-item {
        width: 4.5em;
        flex-direction: column;
        justify-content: start;
        height: 100%;
    }

    .sb-item>span {
        font-size: 1.5em;
        transform: rotate(-270deg);
        opacity: 0;
        width: 0;
        overflow: hidden;
        transition: opacity 0.1s ease-in 0.05s;
    }

    .sb-item:active>.sb-icon {
        transform: translateY(5px);
    }

    .sb-item:active>span {
        transform: rotate(-270deg) translateX(5px);
    }

    .sidebar:hover {
        min-height: 15em;
    }

    .sidebar:hover span {
        opacity: 1;
        overflow: visible;
        overflow: initial;
    }

    .sb-icon {
        min-width: 2.5em;
        min-height: 2.5em;
        margin-top: 1.5em;
    }
}

/*Desktop view*/
@media only screen and (min-width: 1024px) and (min-height: 500px) {
    .sidebar {
        width: 5.5em;
        min-width: 5.5em;
        transition: min-width 0.5s;
        height: 100vh;
        flex-direction: column;
    }

    .sb-item>span {
        transform: rotate(0deg);
    }

    .sidebar>div {
        flex-direction: column;
    }

    .sb-item {
        flex-direction: row;
        width: 100%;
        height: 4.5em;
        justify-content: start;
    }

    .sb-item:active>span {
        transform: translateY(5px);
    }

    .sidebar:hover {
        min-width: 15em;
        height: 100vh;
    }

    .sb-icon {
        margin-left: 1.5em;
        margin-top: 0;
    }
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-family: 'Muli', sans-serif;
    -webkit-tap-highlight-color: transparent;
}

#root {
    min-height: 200vh;
    display: flex;
    flex-direction: column;
}

.content {
    background-color: rgb(99, 0, 138);
    flex: 1 1;
}

.hidden {
    visibility: hidden;
}

::-webkit-scrollbar {
    width: 10px;
    background-color: rgb(99, 0, 138);
}

::-webkit-scrollbar-track {
    background: whitesmoke;
    border-radius: 10px;
    box-shadow: inset 0 0 5px grey;
}

::-webkit-scrollbar-thumb {
    background: rgb(231, 73, 99);
    border-radius: 10px;
}

::selection {
    color: rgb(231, 73, 99);
    background-color: rgb(99, 0, 138);
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
}

/*Modern Mobile*/
@media only screen and (min-width: 520px) {
    .content {
        margin-top: 5.5em;
    }
}

/*Desktop view*/
@media only screen and (min-width: 1024px) and (min-height: 500px) {
    .content {
        margin-top: 0;
        margin-left: 5.5em;
    }
}
